import React from "react";
import _ from "lodash";
import * as moment from "moment";
import { Badge } from "reactstrap";
import {
  getDateFormat,
  getDateTime,
  getUserFullName,
  getStatusInfo,
  fixNumber,
} from "helpers/UtilsHelper";
import avatar from "../../../assets/img/theme/default-avatar.png";
import cons from "../../../constants";

export default function Provider(props) {
  const { item, selectedItemId, check, openSideBar, setStatus } = props;

  const fullName = getUserFullName(item);
  const status = getStatusInfo(cons.PROVIDER_STATUS_INFO, item.status);
  const totalWeekHours = moment
    .duration({ seconds: item.totalWeekHours })
    .asHours();
  const totalHours = moment.duration({ seconds: item.totalHours }).asHours();
  const avgBookingPerMonth = moment
    .duration({ seconds: item.avgBookingPerMonth })
    .asHours();
  const tempProviderTypesArr =
    !_.isNull(item.providerTypes) && item.providerTypes.split(",");
  const providerType =
    tempProviderTypesArr &&
    tempProviderTypesArr.map((type) => {
      const type_obj = cons.PROVIDER_TYPE.find(
        (lib) => lib.id === Number(type)
      );
      return type_obj && type_obj.provider_type;
    });

  return (
    <tr
      className={`provider ${
        selectedItemId && item.id === selectedItemId ? "provider__selected" : ""
      }`}
    >
      <th className="text-center">
        <div className="custom-control custom-checkbox">
          <input
            className="custom-control-input"
            id={item.id}
            type="checkbox"
            checked={!!item.checked}
            onChange={check}
          />
          <label className="custom-control-label" htmlFor={item.id} />
        </div>
      </th>
      <td className="table-actions text-center">
        <img
          alt="..."
          className="avatar rounded-circle"
          src={item.avatar || avatar}
        />
      </td>
      <td
        className="table-user provider__name"
        onClick={(e) => openSideBar(e, item.id, true, providerType)}
      >
        <b>{fullName}</b>
      </td>
      <td className="table-actions text-center">
        {providerType ? providerType.join(", ") : ""}
      </td>

      <td className="table-actions text-center">
        <Badge
          color={status.color}
          pill
          onClick={(e) => setStatus(e, item.id)}
          className="cursor-pointer"
        >
          <b>{status.status || "Irvine"}</b>
        </Badge>
      </td>

      <td className="table-actions text-center">
        {getDateTime(item.dateJoined)}
      </td>
      <td className="table-actions text-center">{totalWeekHours}</td>
      <td className="table-actions text-center">{totalHours}</td>
      <td className="table-actions text-center">
        {fixNumber(avgBookingPerMonth)}
      </td>
      <td className="table-actions text-center">
        {fixNumber(item.avgCancelledPerMonth, 1)}
      </td>
    </tr>
  );
}
