import React from "react";
import * as moment from "moment";
import { Button, Row, Col } from "reactstrap";
import { Link } from "react-router-dom";
import { getDateTime, getUserFullName } from "../../../helpers/UtilsHelper";
import PerfectScrollbar from "react-perfect-scrollbar";
import avatar from "../../../assets/img/theme/default-avatar.png";

export default function ProfileSideBar(props) {
  const { showSidebar, dataSideBar, toggle, setStatus, provType } = props;
  const fullName = dataSideBar && getUserFullName(dataSideBar);
  const providerType = dataSideBar && dataSideBar.providerTypes;

  const totalWeekHours =
    dataSideBar &&
    moment.duration({ seconds: dataSideBar.totalWeekHours }).asHours();
  return (
    <>
      {dataSideBar ? (
        <div id="sidebar" className="sidebar sidebar__wrapper wrapper">
          <div
            className={`sidebar ${
              showSidebar ? "sidebar-show" : "sidebar-hide"
            }`}
          >
            <PerfectScrollbar>
              <div className="sidebar__main">
                <span className={showSidebar ? "sidebar__icon" : "d-none"}>
                  <Button
                    onClick={toggle}
                    className="sidebar__icon"
                    color="link"
                  >
                    <i className="fas fa-times fa-lg"></i>
                  </Button>
                </span>
                <div className="sidebar__body">
                  <img
                    alt="User Avatar"
                    className="customs avatar rounded-circle"
                    src={dataSideBar.avatar || avatar}
                  />
                  <h4 className="sidebar__body__name"> {fullName} </h4>
                  {/* <p className="sidebar__body__sub">Provider type</p> */}
                  <p className="sidebar__body__sub">
                    {provType ? provType.join(", ") : ""}
                  </p>
                  <div className="my-4">
                    <Link to={`/admin/providers/${dataSideBar.id || ""}`}>
                      <Button className="action-btn" color="primary">
                        {" "}
                        Edit
                      </Button>
                    </Link>
                    <Button
                      className="ml-2 action-btn"
                      color="secondary"
                      onClick={(e) => setStatus(e, dataSideBar.id)}
                    >
                      Set Status
                    </Button>
                  </div>
                </div>

                <div className="border-bottom ml-4 mb-5">
                  <Col className="mb-2">
                    <Row>
                      <h3 className="sidebar__body__title">
                        {" "}
                        <i className="far fa-user mr-2 fa-sm"></i> Account{" "}
                      </h3>
                    </Row>
                    <Row>
                      <h6 className="sidebar__body__sub ml-4">
                        {" "}
                        Joined{" "}
                        {dataSideBar.dateJoined
                          ? getDateTime(dataSideBar.dateJoined, "ll")
                          : "Irvine"}{" "}
                      </h6>
                    </Row>
                  </Col>
                  <Col className="mb-2">
                    <Row>
                      <h3 className="sidebar__body__title">
                        <i className="fas fa-mobile-alt mr-2 fa-sm"></i> Phone
                        Number{" "}
                      </h3>
                    </Row>
                    <Row>
                      <h6 className="sidebar__body__sub ml-4">
                        {" "}
                        {dataSideBar.phoneNumber || "Irvine"}{" "}
                      </h6>
                    </Row>
                  </Col>
                  <Col className="mb-2">
                    <Row>
                      <h3 className="sidebar__body__title">
                        {" "}
                        <i className="far fa-envelope mr-2 fa-sm"></i>Email{" "}
                      </h3>
                    </Row>
                    <Row>
                      <h6 className="sidebar__body__sub ml-4">
                        {" "}
                        {dataSideBar.email || "Irvine"}{" "}
                      </h6>
                    </Row>
                  </Col>
                  {/* <Col className="mb-2">
                    <Row>
                      <h3 className="sidebar__body__title">
                        <i className="fas fa-map-marker-alt mr-2 fa-sm"></i>{" "}
                        Address{" "}
                      </h3>
                    </Row>
                    <Row>
                      <h6 className="sidebar__body__sub ml-4">
                        {" "}
                        {dataSideBar.address || "Irvine"}{" "}
                      </h6>
                    </Row>
                  </Col> */}
                  <Col className="mb-2">
                    <Row>
                      <h3 className="sidebar__body__title">
                        <i className="fas fa-city mr-2 fa-sm"></i> Residing City{" "}
                      </h3>
                    </Row>
                    <Row>
                      <h6 className="sidebar__body__sub ml-4">
                        {" "}
                        {dataSideBar.address || "Irvine"}{" "}
                      </h6>
                    </Row>
                  </Col>
                  <Col className="mb-2">
                    <Row>
                      <h3 className="sidebar__body__title">
                        <i className="fas fa-chalkboard-teacher mr-2 fa-sm"></i>{" "}
                        Professional Title{" "}
                      </h3>
                    </Row>
                    <Row>
                      <Col className="form-col">
                        <h6 className="sidebar__body__sub ml-4">
                          {" "}
                          {dataSideBar.headline || "Irvine"}{" "}
                        </h6>
                      </Col>
                    </Row>
                  </Col>
                  {/* <Col className="mb-2">
                    <Row>
                      <h3 className="sidebar__body__title">
                        <i className="fas fa-atlas mr-2 fa-sm"></i> Profile Bio{" "}
                      </h3>
                    </Row>
                    <Row>
                      <Col className="form-col">
                        <h6 className="sidebar__body__sub ml-4">
                          {" "}
                          {dataSideBar.biography || "Irvine"}{" "}
                        </h6>
                      </Col>
                    </Row>
                  </Col> */}
                  {/* <Col>
                    <Row>
                      <Col className="form-col">
                        <h3 className="sidebar__body__title">
                          <i className="fas fa-external-link-alt fa-sm"></i>{" "}
                          Website, Linkedin, Psychology Today, or Social Media
                          Link
                        </h3>
                      </Col>
                    </Row>
                    <Row>
                      <h6 className="sidebar__body__sub ml-4">
                        {dataSideBar.socialLinks ? (
                          <a
                            href={dataSideBar.socialLinks}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="external-site"
                          >
                            {dataSideBar.socialLinks}
                          </a>
                        ) : (
                          "None"
                        )}
                      </h6>
                    </Row>
                  </Col> */}
                </div>
                {/* <div className="ml-4">
                  <Col className="mb-3">
                    <Row>
                      <h3 className="sidebar__body__title">
                        <i className="fas fa-file-invoice-dollar mr-2" />{" "}
                        Invoices{" "}
                      </h3>
                    </Row>
                    <Row>
                      <Link
                        to="/admin/billings/booking-invoice"
                        className="sidebar__body__sub ml-4"
                      >
                        Go to billing page
                      </Link>
                    </Row>
                  </Col>
                  <Col className="mb-3">
                    <Row>
                      <h3 className="sidebar__body__title">
                        <i className="fas fa-history mr-2"></i>{" "}
                        weekly-average-total hours{" "}
                      </h3>
                    </Row>
                    <Row>
                      <h6 className="sidebar__body__sub ml-4">
                        {" "}
                        {totalWeekHours} Hours{" "}
                      </h6>
                    </Row>
                  </Col>
                </div> */}
              </div>
            </PerfectScrollbar>
          </div>
        </div>
      ) : (
        ""
      )}
    </>
  );
}
