import React, { Component } from "react";
import { connect } from "react-redux";
import classNames from "classnames/bind";
import {
  Button,
  Card,
  Table,
  Row,
  Col,
  CardHeader,
  Input,
  Form,
  Label,
  ButtonGroup,
  Modal,
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from "reactstrap";
import _ from "lodash";
import {
  getList,
  tickOnCheckbox,
  tickOnAllCheckbox,
  update,
  updateStatus,
  deleteList,
} from "../../../redux/actions/providerAction";
import Provider from "./Provider";
import ConfirmDeleteModal from "../../../components/Modal/ConfirmDeleteModal.jsx";
import cons from "../../../constants";
import PaginationBar from "../../../components/PaginationBar";
import Loading from "../../../components/Loading";

import "./ListProvider.scss";
import ProfileSideBar from "./ProfileSideBar";
import { Link } from "react-router-dom";

const tabs = { ALL: "", PENDING: 0 };

const defaultState = () => {
  return {
    dropdownProviderTypeName: "Provider Type",
    dropdownStatusName: "Status",
    status: "",
    dropdownOpen: false,
    search: "",
    isLoading: true,
    radios: tabs.ALL,
    isDeleteAccount: false,
    isOpenSideBar: false,
    isShowModalChangeStatus: false,
    listSelectedProviderId: [],
    dataSideBar: null,
    tickCheckAll: false,
    providerUpdateStatusId: "",
    dropdownProviderTypeOpen: false,
    dropdownStatusOpen: false,
  };
};

const defaultFilters = () => {
  return {
    limit: 10,
    offset: 0,
    selected: 0,
    search: "",
    ordering: "first_name",
    desc: true,
    status: "",
    provider_type: "",
  };
};

class ListProvider extends Component {
  constructor(props) {
    super(props);
    this.state = defaultState();
    this.filters = defaultFilters();
    this.searchRef = React.createRef();
    this.delaySearch = _.debounce(this.search, 300);
  }

  componentDidMount() {
    this.props.getList(this.filters);
  }

  sort = (e) => {
    let name = e.target.attributes.name.value;
    if (name && name === this.filters.ordering) {
      this.filters.desc = !this.filters.desc;
    } else {
      this.filters.ordering = name;
      this.filters.desc = true;
    }
    this.resetFilterData();
    this.props.getList(this.filters);
  };

  onSearch = (e) => {
    e.preventDefault();
    const { name, value } = e.target;
    this.setState({ [name]: value });
    this.delaySearch();
  };

  search = () => {
    this.filters.search = this.state.search.trim();
    this.resetFilterData();
    this.props.getList(this.filters);
  };

  selectPageSize = (e) => {
    const { value } = e.target;
    if (value) {
      this.resetFilterData();
      this.setState({ isOpenSideBar: false, tickCheckAll: false });
      this.filters.limit = value;
      this.props.getList(this.filters);
    }
  };

  selectPage = ({ selected }) => {
    this.filters.selected = selected || 0;
    this.filters.offset = Math.ceil(selected * this.filters.limit);
    this.setState({
      isOpenSideBar: false,
      tickCheckAll: false,
      dataSideBar: null,
    });
    this.props.getList(this.filters);
  };

  resetFilterData = () => {
    this.filters.offset = 0;
    this.filters.selected = 0;
  };

  clearFilterData = () => {
    this.filters = defaultFilters();
    this.setState(defaultState());
    this.props.getList(this.filters);
  };

  deleteProviderAccount = () => {
    const ids = this.props.providerData.result
      .filter((item) => item.checked)
      .map((item) => item.id);
    const data = {
      ids,
    };
    this.props.deleteList(data);
    this.toggleConfirmDelete();
    this.toggleSideBar();
  };

  updateProviderAccount = () => {
    const { status, providerUpdateStatusId } = this.state;
    const params = { status, id: providerUpdateStatusId };
    this.props.updateStatus(params);
    this.toggleChangeStatus();
  };

  handleCheckbox = (e) => {
    const { checked, id } = e.target;
    this.props.tickOnCheckbox(checked, id);
  };

  handleAllCheckbox = (e) => {
    const { checked } = e.target;
    this.setState({ tickCheckAll: checked }, () => {
      this.props.tickOnAllCheckbox(checked);
    });
  };

  filter = (e, itemName) => {
    const { value, name } = e.target;
    this.filters[name] = value;
    if (name === "provider_type") {
      this.setState({ dropdownProviderTypeName: itemName });
    } else {
      if (value === String(cons.PROVIDER_STATUS.PENDING)) {
        this.setState({ radios: tabs.PENDING });
      } else {
        this.setState({ radios: tabs.ALL });
      }
      this.setState({ dropdownStatusName: itemName });
    }
    this.props.getList(this.filters);
  };

  handleRadios = (radios) => {
    this.resetFilterData();
    this.setState({ radios });
    if (radios === tabs.ALL) {
      this.filters.status = "";
      this.setState({ dropdownStatusName: "All" });
      this.props.getList(this.filters);
    } else {
      this.setState({ dropdownStatusName: "Pending" });
      this.filters.status = 0;
      this.props.getList(this.filters);
    }
  };

  handleChangeStatus = (e) => {
    const { value, name } = e.target;
    this.setState({
      [name]: Number(value),
    });
  };

  handleFilterChange = (e, value) => {
    this.resetFilterData();
    this.filter(e, value);
  };

  getProviderUpdateStatus = (e, id) => {
    this.setState({ providerUpdateStatusId: id });
    this.toggleChangeStatus();
  };

  toggleChangeStatus = () => {
    this.setState((prevState) => ({
      isShowModalChangeStatus: !prevState.isShowModalChangeStatus,
    }));
  };

  toggleAction = () => {
    this.setState((prevState) => ({
      dropdownOpen: !prevState.dropdownOpen,
    }));
  };

  toggleConfirmDelete = () => {
    this.setState({ isDeleteAccount: !this.state.isDeleteAccount });
  };

  toggleSideBar = (e, id, state, provider) => {
    const providerArr = this.props.providerData.result;
    let dataSideBar = providerArr.find((item) => item.id === id);

    this.setState({
      isOpenSideBar: state || !this.state.isOpenSideBar,
      dataSideBar,
      provider,
    });
  };

  toggleSelectProviderType = () => {
    this.setState({
      dropdownProviderTypeOpen: !this.state.dropdownProviderTypeOpen,
    });
  };

  toggleSelectStatus = () => {
    this.setState({ dropdownStatusOpen: !this.state.dropdownStatusOpen });
  };

  render() {
    const {
      search,
      radios,
      tickCheckAll,
      isShowModalChangeStatus,
      providerUpdateStatusId,
      dropdownProviderTypeName,
      dropdownProviderTypeOpen,
      dropdownStatusName,
      dropdownStatusOpen,
    } = this.state;
    const { providerData, loading } = this.props;
    const providerArr = providerData.result;
    const countCheck =
      providerArr &&
      providerArr.filter((item) => item.checked).map((item) => item.id).length;
    const updatingProvider =
      providerArr &&
      providerArr.find((item) => item.id === providerUpdateStatusId);
    const defaultStatus = updatingProvider && updatingProvider.status;
    const isDisableButton = countCheck > 0 ? false : true;
    const { limit, selected } = this.filters;

    const totalPage = Math.ceil(providerData.count / limit) || 0;
    const paginationInfo = {
      from: selected * limit + 1,
      to:
        (selected + 1) * limit < providerData.count
          ? (selected + 1) * limit
          : providerData.count,
      total: providerData.count,
      getShowOne: function () {
        return this.from === this.to;
      },
    };
    const paginationSetting = {
      totalPage,
      selected: this.filters.selected,
    };
    const sortClass = {
      title: this.filters.ordering,
      value: this.filters.desc,
      get: function (name) {
        if (this.title === name) {
          return `sort ${this.value ? "sort-desc" : "sort-asc"}`;
        }
        return "sort";
      },
    };
    return (
      <>
        <Col className="list-provider">
          <Card className="mt-3">
            <ConfirmDeleteModal
              className="modal-dialog-centered"
              isOpen={this.state.isDeleteAccount}
              toggle={this.toggleConfirmDelete}
              handle={this.deleteProviderAccount}
              content="Are you sure tou want to permanently delete the selected account(s)?"
            />
            <ProfileSideBar
              showSidebar={this.state.isOpenSideBar}
              toggle={this.toggleSideBar}
              dataSideBar={this.state.dataSideBar}
              provType={this.state.provider}
              setStatus={this.getProviderUpdateStatus}
            />
            <Modal
              className="modal-dialog-centered"
              isOpen={isShowModalChangeStatus}
              toggle={this.toggleChangeStatus}
              size="sm"
            >
              <div className="modal-header">
                <h6 className="modal-title" id="modal-title-default">
                  Change Status for this Provider
                </h6>
                <button
                  aria-label="Close"
                  className="close"
                  data-dismiss="modal"
                  type="button"
                  onClick={this.toggleChangeStatus}
                >
                  <span aria-hidden={true}>×</span>
                </button>
              </div>
              <div className="modal-body text-center">
                <Input
                  type="select"
                  onChange={this.handleChangeStatus}
                  name="status"
                  defaultValue={defaultStatus || ""}
                >
                  {cons.PROVIDER_STATUS_INFO.map((item, i) => {
                    return item.id !== "" ? (
                      <option key={i} value={item.id}>
                        {item.status}
                      </option>
                    ) : (
                      ""
                    );
                  })}
                </Input>
              </div>
              <div className="modal-footer">
                <Button
                  color="success"
                  outline
                  type="button"
                  onClick={this.updateProviderAccount}
                >
                  Save Change
                </Button>
                <Button
                  className="ml-auto"
                  color="link"
                  data-dismiss="modal"
                  type="button"
                  onClick={this.toggleChangeStatus}
                >
                  Close
                </Button>
              </div>
            </Modal>
            {loading && <Loading />}
            <CardHeader className="bg-transparent border-0">
              {/* <Row className="d-flex align-items-center justify-content-center">
                <ButtonGroup
                  className="btn-group-toggle mb-3"
                  data-toggle="buttons"
                >
                  <Button
                    className={`radio-customs ${
                      radios === tabs.ALL ? "active" : ""
                    }`}
                    color="default"
                    outline
                    onClick={() => this.handleRadios(tabs.ALL)}
                  >
                    <Input
                      name="options"
                      type="radio"
                      value={radios === tabs.ALL}
                    />
                    All
                  </Button>
                  <Button
                    className={`radio-customs ${
                      radios === tabs.PENDING ? "active" : ""
                    }`}
                    color="default"
                    outline
                    onClick={() => this.handleRadios(tabs.PENDING)}
                  >
                    <Input
                      name="options"
                      type="radio"
                      value={radios === tabs.PENDING}
                    />
                    Pending
                  </Button>
                </ButtonGroup>
              </Row> */}
              <Row className="d-flex">
                <Col md="12" lg="7" className="mb-3">
                  <Row className="align-items-center">
                    <Col className="mt-3 mt-md-0 justify-content-center">
                      <div className="d-none d-lg-inline-block">
                        <b className="m-md-3">Filters:</b>
                      </div>
                      <Dropdown
                        isOpen={dropdownProviderTypeOpen}
                        toggle={this.toggleSelectProviderType}
                        className="mb-2 mb-md-0 list-provider__dropdown"
                      >
                        <DropdownToggle caret>
                          <i className="fas fa-filter mr-2" />
                          {dropdownProviderTypeName}
                        </DropdownToggle>
                        <DropdownMenu>
                          <DropdownItem
                            value=""
                            name="provider_type"
                            onClick={(e) => this.filter(e, "All")}
                          >
                            All
                          </DropdownItem>
                          {cons.PROVIDER_TYPE.map((item) => {
                            return (
                              <DropdownItem
                                key={item.id}
                                name="provider_type"
                                onClick={(e) =>
                                  this.handleFilterChange(e, item.provider_type)
                                }
                                value={item.id}
                              >
                                {item.provider_type}
                              </DropdownItem>
                            );
                          })}
                        </DropdownMenu>
                      </Dropdown>
                      <Dropdown
                        isOpen={dropdownStatusOpen}
                        toggle={this.toggleSelectStatus}
                        className="mb-2 mb-md-0 list-provider__dropdown"
                      >
                        <DropdownToggle caret>
                          <i className="fas fa-filter mr-2" />
                          {dropdownStatusName}
                        </DropdownToggle>
                        <DropdownMenu>
                          <DropdownItem
                            value=""
                            name="status"
                            onClick={(e) => this.filter(e, "All")}
                          >
                            All
                          </DropdownItem>
                          {cons.PROVIDER_STATUS_INFO.map((item) => {
                            return (
                              <DropdownItem
                                key={item.id}
                                name="status"
                                onClick={(e) =>
                                  this.handleFilterChange(e, item.status)
                                }
                                value={item.id}
                              >
                                {item.status}
                              </DropdownItem>
                            );
                          })}
                        </DropdownMenu>
                      </Dropdown>
                      <Button
                        color="secondary"
                        outline
                        onClick={this.clearFilterData}
                        className="align-items-center btn-clean"
                      >
                        <b>Clear</b>
                        <i className="fas fa-eraser  mt-1"></i>
                      </Button>
                    </Col>
                  </Row>
                </Col>

                <Col md="12" lg="5" className="mb-3">
                  <Row className="align-items-center justify-content-start">
                    <Col>
                      <Form onSubmit={this.onSearch}>
                        <Input
                          type="text"
                          name="search"
                          value={search}
                          placeholder="Search..."
                          ref={(ref) => (this.searchRef = ref)}
                          onChange={this.onSearch}
                        />
                      </Form>
                    </Col>
                  </Row>
                </Col>
              </Row>
            </CardHeader>
            <Table className="align-items-center table-sort" responsive>
              <thead className="thead-light">
                <tr>
                  <th className="text-center table__header__checkbox">
                    <div className="custom-control custom-checkbox">
                      <input
                        className="custom-control-input"
                        id="table-check-all"
                        type="checkbox"
                        checked={tickCheckAll}
                        onChange={this.handleAllCheckbox}
                      />
                      <label
                        className="custom-control-label"
                        htmlFor="table-check-all"
                      />
                    </div>
                  </th>
                  <th className="table__header__w-95" />
                  <th
                    className={`table__header__w-95 ${sortClass.get(
                      "first_name"
                    )}`}
                    name="first_name"
                    onClick={this.sort}
                  >
                    {" "}
                    Name{" "}
                  </th>
                  <th className="text-center"> Provider Type </th>
                  <th
                    className={`text-center table__header__w-95 ${sortClass.get(
                      "status"
                    )}`}
                    name="status"
                    onClick={this.sort}
                  >
                    {" "}
                    status{" "}
                  </th>
                  <th
                    className={`text-center table__header__w-95 ${sortClass.get(
                      "date_joined"
                    )}`}
                    name="date_joined"
                    onClick={this.sort}
                  >
                    {" "}
                    Joined{" "}
                  </th>
                  <th
                    className={classNames(
                      "text-center",
                      sortClass.get("total_week_hours")
                    )}
                    name="total_week_hours"
                    onClick={this.sort}
                  >
                    {" "}
                    Bookings This Week{" "}
                  </th>

                  <th
                    className={classNames(
                      "text-center",
                      sortClass.get("total_hours")
                    )}
                    name="total_hours"
                    onClick={this.sort}
                  >
                    {" "}
                    Total Bookings{" "}
                  </th>
                  <th
                    className={classNames(
                      "text-center",
                      sortClass.get("avg_booking_per_month")
                    )}
                    name="avg_booking_per_month"
                    onClick={this.sort}
                  >
                    {" "}
                    Bookings per Month{" "}
                  </th>
                  <th
                    className={classNames(
                      "text-center",
                      sortClass.get("avg_cancelled_per_month")
                    )}
                    name="avg_cancelled_per_month"
                    onClick={this.sort}
                  >
                    {" "}
                    Cancellations per Month{" "}
                  </th>
                </tr>
              </thead>
              <tbody className="client">
                {providerArr &&
                  providerArr.map((item, i) => {
                    return (
                      <Provider
                        key={i}
                        selectedItemId={
                          this.state.dataSideBar && this.state.dataSideBar.id
                        }
                        check={this.handleCheckbox}
                        openSideBar={this.toggleSideBar}
                        item={item}
                        setStatus={this.getProviderUpdateStatus}
                      />
                    );
                  })}
              </tbody>
            </Table>
            {providerArr && providerArr.length > 0 && (
              <Row className="d-flex justify-content-md-between justify-content-sm-start mb-3 mt-3 mx-0">
                <Col className="d-flex justify-content-md-center justify-content-sm-start">
                  {totalPage > 1 && (
                    <PaginationBar
                      setting={paginationSetting}
                      handleClick={this.selectPage}
                    />
                  )}
                </Col>
                <Col
                  className="d-flex align-items-baseline mb-md-0 mb-2"
                  xs="auto"
                >
                  <Input
                    onChange={this.selectPageSize}
                    value={limit}
                    bsSize="md"
                    type="select"
                    name="selectPageSize"
                    id="selectPageSize"
                  >
                    <option value="5">5</option>
                    <option value="10">10</option>
                    <option value="20">20</option>
                    <option value="50">50</option>
                  </Input>
                  {/* <Label className="ml-1 text-nowrap">
                    <small>
                      Page
                      {` ${paginationInfo.from} ${
                        paginationInfo.getShowOne()
                          ? ""
                          : `- ${paginationInfo.to}`
                      } of ${paginationInfo.total}`}
                    </small>


                    
                  </Label> */}

                  <Label className="ml-1 text-nowrap">
                    <small>
                      Page {`${selected + 1}`} of {`${totalPage}`}
                    </small>
                  </Label>
                </Col>
              </Row>
            )}
          </Card>
        </Col>
        <Row className="m-3">
          <Col lg={4} className="text-center">
            <Button color="secondary" disabled={isDisableButton}>
              Message
            </Button>{" "}
          </Col>
          <Col lg={4} className="text-center">
            <Link to="/admin/providers/create">
              <Button color="info">Add New</Button>
            </Link>
          </Col>
          <Col lg={4} className="text-center">
            <Button
              color="danger"
              onClick={this.toggleConfirmDelete}
              disabled={isDisableButton}
            >
              {" "}
              Remove{" "}
            </Button>
          </Col>
        </Row>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    providerData: state.provider.data,
    loading: state.provider.loading,
  };
};

const mapActionToProps = {
  getList,
  tickOnCheckbox,
  tickOnAllCheckbox,
  update,
  updateStatus,
  deleteList,
};

export default connect(mapStateToProps, mapActionToProps)(ListProvider);
